@charset "UTF-8";

@import "module/variable";
@import "module/utility";
@import "module/animation";
@import "module/vendor";
@import "module/gradation";
@import "module/fonts";
@import "module/editor";
@import "//maxcdn.bootstrapcdn.com/font-awesome/4.3.0/css/font-awesome.min.css";

body {
  &.mce {
    &-content-body,&ContentBody {
      max-width: 768px;
      @include family(normal);
      font-size: 88% !important;
      line-height: 1;
    }
  }
  ul,ol,li {
    margin: 0;
    padding: 0;
  }
}

.editor-area {
  max-width: 100%;
  padding: 10px;
  color: $text-color;
  @extend %editor-area;
}

/* WPクラス */
/* キャプション付き */
div {
  &.align {
    &center {
      margin-left: auto;
      margin-right: auto;
    }
    &left {
      float: left;
    }
    &right {
      float: right;
    }
  }
}
