%editor-area {
	@include family(normal);
	font-size: 100%;
	h1,h2,h3,h4,h5,h6,p,a {
		img {
			max-width: 100%;
			height: auto;
		}
  }
  h1,h2,h3,h4,h5,h6 {
    margin: 3em 0 16px;

    &:first-child {
      margin-top: 0;
    }
  }
	h2,h3,h4,h5,h6 {
		padding: 3px;
		line-height: 1.2;
	}
	& > h1 {
		font-size: 180%;
	}
	& > h2 {
		font-size: 160%;
		color: $primary-color;
		border-bottom: 2px solid $primary-color;
	}
	& > h3 {
		font-size: 140%;
		border-bottom: 2px dashed #555;
	}
	h4,h5,h6 {
		font-size: 120%;
		border-bottom: 1px dotted #555;
	}
	// table
	table {
		max-width: 100%;
		margin-top: 14px;
		margin-bottom: 14px;
		tr {

		}
		th,td {
			padding: 4px 6px;
			border: 1px solid #ccc;
		}
		th {
			background: #f0f0f0;
		}
		td {
			background: #fff;
		}
	}
	dl {
		background-color: #fff;
		border: 1px solid #ccc;
		dt {
			padding: 10px;
			font-weight: bold;
			font-size: 110%;
			background-color: #f0f0f0;
		}
		dd {
			padding: 10px 10px 24px 24px;
		}
	}
	strong {
		font-weight: bold;
	}
	em {
		font-style: italic;
		font-style: oblique;

  }
  p {
    margin: 0 0 2em;
    line-height: 1.8;
    font-size: 100%;

    &:first-child {
      margin-top: 0;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }

	a {
		color: $primary-color;
		text-decoration: underline;
		&:hover {
			opacity: .8;
			text-decoration: none;
		}
		&[target="_blank"] {
			&:after {
				content: "\f08e";
				display: inline-block;
				margin-left: 4px;
				font-family: 'FontAwesome';
				font-size: 88%;
				text-rendering: auto;
				-webkit-font-smoothing: antialiased;
				-moz-osx-font-smoothing: grayscale;
				transform: translate(0, 0);
			}
		}
	}
	blockquote {
		position: relative;
		margin: 1em 8px 2em 0;
		padding: 14px 20px;
		font-size: 88%;
		background-color: #f5f5f5;
    border-left: 5px solid #ccc;
    box-shadow: 9px 9px 0 -3px #ededed;

    p {
      margin-bottom: 1em;
    }
	}
	abbr,acronym {
		position: relative;
		&[title] {
			border-bottom: 1px dotted #999;
			&:after {
				content: " ";
				position: absolute;
				left: 0;
				bottom: 100%;
				display: inline-block;
				margin-bottom: 6px;
				padding: 2px 6px;
				line-height: 1.4;
				opacity: 0;
				background-color: #c9e5ef;
				border: 1px solid darken(#c9e5ef, 10%);
				border-radius: 4px;
				box-shadow: 1px 1px 3px 0 rgba(#666, .3);
				transition: 180ms ease-in-out;
			}
			&:hover {
				&:after {
					content: attr(title);
					display: inline-block;
					opacity: 1;
				}
			}
		}
	}
	code {
		font-size: 100%;
		background-color: #ffc;
	}
	kbd {
		font-size: 100%;
		background-color: #cef;
	}
	pre {
		margin-top: 1em;
		margin-bottom: 1em;
    padding: 10px;
    line-height: 1.4;
    font-family: monospace;
    font-size: 100%;
    letter-spacing: 1px;
		white-space: pre-wrap;
		background-color: #fafafa;
		border: 1px solid #ccc;
	}
	q {
		&:before {
			content: "\201c";
		}
		&:after {
			content: "\201d";
		}
	}
	sub,sup {
		font-size: 80%;
  }
  ul,
  ol {
    padding-left: 16px;
  }
  > ul,
  > ol {
    margin: 2em 0;
		font-size: 108%;
    line-height: 1.6;
	}
	address {
		margin-top: 1em;
		margin-bottom: 1em;
		padding: 10px;
		background-color: #fff;
		border: 1px solid #ccc;
	}
	img {
		max-width: 100%;
		height: auto;
		margin-bottom: 10px;
		vertical-align: top;
		&.alignright {
			display: inline;
			margin-left: 14px;
		}
		&.alignleft {
			display: inline;
			margin-right: 14px;
		}
	}
	video {
		max-width: 100%;
		height: auto;
	}
	.wp-video {
		margin-top: 24px;
		margin-bottom: 24px;
	}
	iframe {
		max-width: 100%;
	}
	.iframe {
		position: relative;
		margin-left: auto;
		margin-right: auto;
		iframe {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
		}
		&-video { // 16:9
			max-width: 86%;
			width: 100%;
			padding-bottom: 48.375%;
		}
		&-gmap {
			max-width: 86%;
			width: 100%;
			padding-bottom: 54.25%;
		}
		@media (max-width: $screen-tab) {
			&-video {
				max-width: 100%;
				width: 100%;
				padding-bottom: 56.25%;
			}
			&-gmap {
				max-width: 100%;
				width: 100%;
				padding-bottom: 62.375%;
			}
		}
	}
	.wp-caption {
		max-width: 100%;
		padding: 8px;
		text-align: center;
		background: #f0f0f0;
		border: 1px solid #ccc;
		border-radius: 4px;
		box-shadow: 0 0 5px 1px rgba(#666, .2);
		&.alignleft {
			margin-right: 14px;
		}
		&.alignright {
			margin-left: 14px;
		}
		&.aligncenter,&.alignnone {
			margin-bottom: 14px;
		}
		dl {
			dt,dd {
				padding: 0;
			}
		}
		&-dt {
			padding: 0;
		}
		&-dd {
			padding: 0;
			font-size: 92%;
			color: #666;
			text-shadow: 0 1px 0 #fff;
		}
		&-text {
			font-size: 92%;
			color: #666;
			text-shadow: 0 1px 0 #fff;
		}
	}
}
